.language-block{
    display: flex;
    flex-direction: column;
    gap: 40px;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    .title-language{
        text-align: center;
        font-size: 32px;
        font-weight: 600;
    }
    &__btn{
        display: flex;
        gap: 50px;
    }
    .get-language{
        font-size: 18px;
        color: #e1e1e1;
        font-family: inherit;
        font-weight: 800;
        cursor: pointer;
        position: relative;
        border: none;
        background: none;
        text-transform: uppercase;
        transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
        transition-duration: 400ms;
        transition-property: color;
      
      &:focus,
      &:hover {
        color: #fff;
      }
      
      &:focus:after,
      &:hover:after {
        width: 100%;
        left: 0%;
      }
      
      &:after {
        content: "";
        pointer-events: none;
        bottom: -2px;
        left: 50%;
        position: absolute;
        width: 0%;
        height: 2px;
        background-color: #fff;
        transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
        transition-duration: 400ms;
        transition-property: width, left;
      }
}
}
