.navbar{
    position: fixed;
    z-index: 555;
    background-color: hsl(0, 0%, 8%,0.2);
    width: 100%;
    transition: 0.5s;
    &.scrolled{
        transition: 0.5s;
        background-color: hsl(0, 0%, 8%,0.9);
    }    
    &__container{
        position: relative;
        z-index: 10;
        max-width: 1400px;
        padding: 5px 15px;
        margin: 0 auto;
        display: flex;
        align-items: center;
        @include adaptiv-value(gap, 15, 10, 1);
        transition: 0.3s;
        &::after{
            content: '';
            transition: 0.3s;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 60px;
            pointer-events: none;
            z-index: 5;
            background-color: hsl(0, 0%, 8%,0);
        }
        @media (max-width:840px) {
            gap:15px;
            &.active{
                transition: 0.3s;
                &::after{
                    content: '';
                    background-color: #141414;
                }
            }
        }
    }
    &__list{
        display: flex;
        text-transform: uppercase;
        justify-content: center;
        flex: 1 1 auto;
        @media (max-width:1078px) {
            flex:none;
            &.defoult{display: none;}
        }
    }
    &__list-block{
        display: flex;
        flex-wrap: wrap;
        @include adaptiv-value(gap, 25, 5, 1);
        @media (max-width:1150px) {
            gap: 5px;
        }
        @media (max-width:1078px) {
            // gap: 30px;
            flex-direction: column;
        }
    }
    &__list-elemet{
            display: inline-block;
            color: black;
            text-decoration: none;
            cursor: pointer;
            outline: none;
            border: none;
            background: transparent;
            & a{
                height: auto;
                float: left;
                transition: .5s linear;
                position: relative;
                display: block;
                overflow: hidden;
                color: #e9aaaa;
                @include adaptiv-value(padding-top, 10, 10, 1);
                @include adaptiv-value(padding-bottom, 10, 10, 1);
                @include adaptiv-value(padding-left, 15, 10, 1);
                @include adaptiv-value(padding-right, 15, 10, 1);
                @include adaptiv-value(font-size, 16, 10, 1);
                text-align: center;
                margin: 0 5px;
                background: transparent;
                text-transform: uppercase;
                font-weight: 600;
                @media (max-width:1078px) {
                    width: 100%;
                    padding: 20 25;
                    font-size: 20px;   
                }
              &:before {
                position: absolute;
                content: '';
                left: 0;
                bottom: 0;
                height: 2px;
                width: 100%;
                border-bottom: 2px solid transparent;
                border-left: 2px solid transparent;
                box-sizing: border-box;
                transform: translateX(100%);
              }
              &:after {
                position: absolute;
                content: '';
                top: 0;
                left: 0;
                width: 100%;
                height: 2px;
                border-top: 2px solid transparent;
                border-right: 2px solid transparent;
                box-sizing: border-box;
                transform: translateX(-100%);
              }
              
              &:hover {
                box-shadow: 0 3px 15px hsl(0, 59%, 79%,0.5);
              }
              
              &:hover:before {
                border-color: #e9aaaa;
                height: 100%;
                transform: translateX(0);
                transition: .3s transform linear, .3s height linear .3s;
              }
              
              &:hover:after {
                border-color: #e9aaaa;
                height: 100%;
                transform: translateX(0);
                transition: .3s transform linear, .3s height linear .5s;
              }
            }

    }
    &__burger-menu{
        display: none;
        @media (max-width:1078px) {
            position: fixed;
            top: 0;
            right: -100%;
            overflow-y: auto;
            display: flex;
            width: 100%;
            height: 100%;
            flex-direction: column;
            align-items: center;
            gap: 30px;
            transition: 0.5s;
            padding: 15px;
            padding-top: 80px;
            &.active{
                background-color: #333333;
                right: 0;
            }
        }
    }
    &__social{
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        @media (max-width:400px) {
            justify-content: center;
            &.defoult{display: none;}
        }
        & a {
            & img{
                width: 25px;
                height: 25px;
            }
        }
    }
    
    &__logo{
        transition:0.3s;
        position: relative;
        z-index: 10;
        @media (max-width:1078px) {
            flex: 1 1 auto;
        }
        @media (max-width:400px) {
            flex: none;
        }
    }
    &__logo-svg{
            width: 60px;
            height: 50px;
    }        
    &__button-container{
        position: relative;
        z-index: 10;
        opacity: 0;
        transform: scaleY(0);
        transition: 0.3s;
        &.vissible-to-scroll{
            transition: 0.3s;
            opacity: 1;
            transform: scaleY(1);
        }
        @media (max-width:400px) {
            flex: 1 1 auto;
            text-align: center;
        }
    }
        &__reg-button{
            text-decoration: none;
            background-color: hsl(0, 0%, 8%,1);
            border: none;
            &:hover{
                box-shadow: 0 3px 15px hsl(0, 59%, 79%,0.5);
                transition: .3s 0.5s box-shadow linear;
            }
            @media (max-width:840px) {
                margin-left: 0px;
            }
        }
        &__reg-button-a{
            position: relative;
            display: inline-block;
            border: 2px solid #e9aaaa;
            text-transform: uppercase;
            color: #e9aaaa;
            text-decoration: none;
            font-weight: 600;
            font-size: 15px;
            @include adaptiv-value(padding-top, 8, 8, 1);
            @include adaptiv-value(padding-bottom, 8, 8, 1);
            @include adaptiv-value(padding-left, 16, 10, 1);
            @include adaptiv-value(padding-right, 16, 10, 1);
            &::before{
                content: '';
                position: absolute;
                top: 6px;
                left: -2px;
                width: calc(100% + 4px);
                height: calc(100% - 12px);
                background-color: hsl(0, 0%, 8%,1);
                transition: 0.3s ease-in-out;
                transform: scaleY(1);
            }
            &::after{
                content: '';
                position: absolute;
                left: 6px;
                top: -2px;
                height: calc(100% + 4px);
                width: calc(100% - 12px);
                background-color: hsl(0, 0%, 8%,1);
                transition: 0.3s ease-in-out;
                transform: scaleX(1);
                transition-delay: 0.5s;
            }
            &:hover::before{
                transform: scaleY(0);
            }
            &:hover::after{
                transform: scaleX(0);
            }
        }
        &__reg-button-span{
            position: relative;
            z-index: 3;
        }
        &__burger {
            display: none;
            position: relative;
            z-index: 111;
            @media (max-width:1078px) {
                display: inline-block;
            }
        }
        & .menu-btn {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 30px;
            height: 30px;
            background: none;
            & span{
                transition: 0.3s;
                width: 20px;
                height: 4px;
                background-color: #e9aaaa;
                position: absolute;
                border-radius: 10px;
                &::after,
                &::before{
                    content: '';
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    transition: 0.3s;
                    top: 0;
                    left: 0;
                    border-radius: 10px;
                    width: 20px;
                    height: 4px;
                    background-color: #e9aaaa;
                }
                &::after{
                    transform: translateY(-7px);
                }
                &::before{
                    transform: translateY(7px);
                }
            }
            
            &.active{
                & span{
                    width: 0;
                    height: 0;
                    transition: 0.3s;
                    &::after{
                        transform: translatey(-3px) translateX(-10px) rotate(-225deg);
                    }
                    &::before{
                        
                        transform: translateY(-3px) translateX(-10px) rotate(225deg);
                    }
                }
                
            }
        }
}

