        padding: 15px;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    &__title{
        font-size: 42px;
        color: #e9aaaa;
        text-align: center;}
    &__subtitle{
        font-size: 22px;
        text-align: center;
        max-width: 600px;
        margin: 0 auto;
        margin-bottom: 10px;
    }
        &__block{
            display: grid;
            justify-content: space-around;
            gap: 30px;
            grid-template-columns: 45% 45%;
            @media (max-width:1260px) {
                grid-template-columns: 70%;
            }
            @media (max-width:990px) {
                grid-template-columns: 80%;
            }
            @media (max-width:840px) {
                grid-template-columns: 90%;
            }
            @media (max-width:800px) {
                grid-template-columns: auto;
            }
        }
        &__block-card{
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 15px;
            background-color:#333333;
            padding: 20px;
            border-radius: 10px;
            overflow: hidden;
            border: solid 2px #e9aaaa;
            @media (max-width:660px) {
                grid-template-columns: 1fr;
            }
        }
        .block-card{
            align-self: start;
            &__title{
                font-size: 20px;
                position: relative;
                display: flex;
                justify-content: center;
                border: dashed 1px #e9aaaa;
                border-radius: 10px;
                align-self: start;
                padding: 10px;
                & img{
                    display: none;
                    @media (max-width:660px) {
                        display: block;
                        width: 35px;
                        margin-left: 15px;
                        margin-top: -5px;
                    }
                }
            }
            &__text{
                    text-indent: 10px;
                    font-size: 18px;
                    grid-row: 2;
                    
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
            }
            &__img{
                grid-row: 1/3;
                text-align: right;
                @media (max-width:660px) {
                    display: none;
                }
                & img{
                    max-width: 180px;
                }
            }
        }
}
.defence{
    &__container{
        max-width: 1400px;
        margin: 0 auto;
        padding: 15px;
        display: grid;
        grid-template-columns: 55% 40%;
        justify-content:space-between;
        gap: 15px;
        @media (max-width:1078px) {
            display: block;
        }
    }
    .content-defence{
        display: flex;
        flex-direction: column;
        gap: 20px;
        &__title{
            font-size: 42px;
            color: #e9aaaa;
            @media (max-width:460px) {
                font-size: 32px;
            }
            @media (max-width:360px) {
                font-size: 28px;
            }
        }
        &__subtitle{
            text-indent: 10px;
            @media (max-width:1078px) {
                text-indent: 0;
            }
        }
        &__block{
            align-self: center;
            max-width: 600px;
            @media (max-width:1078px) {
                max-width: none;
            }
        }
        &__text{
            text-indent: 10px;
            @media (max-width:1078px) {
                text-indent: 0;
            }
        }
        &__list{
            display: flex;
            flex-direction: column;
            gap: 15px;
            & li{
                position: relative;
                padding-left: 15px;
                &::after{
                    content: '';
                    width: 7px;
                    height: 7px;
                    position: absolute;
                    background-color: #e9aaaa;
                    border-radius: 50%;
                    top: 5px;
                    left: 0;
                }
            }
        }
    }
    &__img{
        @media (max-width:1078px) {
            display: none;
        }
    }
}
