body {
  font-family: "Aileron", sans-serif;
  font-display: swap;
  background-color: #141414;
  height: 100vh;
  overflow-x: hidden;
  line-height: 1.2;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  color: #FFFFFF;
  overflow: hidden;
}

.overflow-hidden {
  overflow: hidden;
}

.language-block {
  display: flex;
  flex-direction: column;
  gap: 40px;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
.language-block .title-language {
  text-align: center;
  font-size: 32px;
  font-weight: 600;
}
.language-block__btn {
  display: flex;
  gap: 50px;
}
.language-block .get-language {
  font-size: 18px;
  color: #e1e1e1;
  font-family: inherit;
  font-weight: 800;
  cursor: pointer;
  position: relative;
  border: none;
  background: none;
  text-transform: uppercase;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 400ms;
  transition-property: color;
}
.language-block .get-language:focus, .language-block .get-language:hover {
  color: #fff;
}
.language-block .get-language:focus:after, .language-block .get-language:hover:after {
  width: 100%;
  left: 0%;
}
.language-block .get-language:after {
  content: "";
  pointer-events: none;
  bottom: -2px;
  left: 50%;
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: #fff;
  transition-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1);
  transition-duration: 400ms;
  transition-property: width, left;
}

.reset-leng {
  color: white;
  background-color: #222;
  font-weight: 500;
  border-radius: 0.5rem;
  font-size: 1rem;
  line-height: 2rem;
  padding-left: 1.4rem;
  padding-right: 1.4rem;
  padding-top: 0.7rem;
  padding-bottom: 0.7rem;
  text-align: center;
  margin-right: 0.5rem;
  display: inline-flex;
  align-items: center;
  border: none;
}
.reset-leng:hover {
  background-color: #333;
}
.reset-leng svg {
  display: inline;
  width: 1.3rem;
  height: 1.3rem;
  margin-right: 0.75rem;
  color: white;
}
.reset-leng:focus svg {
  animation: spin_357 0.5s linear;
}

@keyframes spin_357 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
