.navbar {
  position: fixed;
  z-index: 555;
  background-color: hsla(0, 0%, 8%, 0.2);
  width: 100%;
  transition: 0.5s;
}
.navbar.scrolled {
  transition: 0.5s;
  background-color: hsla(0, 0%, 8%, 0.9);
}
.navbar__container {
  position: relative;
  z-index: 10;
  max-width: 1400px;
  padding: 5px 15px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  gap: 15px;
  transition: 0.3s;
}
@media (max-width: 1400px) {
  .navbar__container {
    gap: calc(10px + 5 * (100vw - 320px) / 1080);
  }
}
.navbar__container::after {
  content: "";
  transition: 0.3s;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  pointer-events: none;
  z-index: 5;
  background-color: hsla(0, 0%, 8%, 0);
}
@media (max-width: 840px) {
  .navbar__container {
    gap: 15px;
  }
  .navbar__container.active {
    transition: 0.3s;
  }
  .navbar__container.active::after {
    content: "";
    background-color: #141414;
  }
}
.navbar__list {
  display: flex;
  text-transform: uppercase;
  justify-content: center;
  flex: 1 1 auto;
}
@media (max-width: 1078px) {
  .navbar__list {
    flex: none;
  }
  .navbar__list.defoult {
    display: none;
  }
}
.navbar__list-block {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
}
@media (max-width: 1400px) {
  .navbar__list-block {
    gap: calc(5px + 20 * (100vw - 320px) / 1080);
  }
}
@media (max-width: 1150px) {
  .navbar__list-block {
    gap: 5px;
  }
}
@media (max-width: 1078px) {
