}
.navbar__list-elemet {
  display: inline-block;
  color: black;
  text-decoration: none;
  cursor: pointer;
  outline: none;
  border: none;
  background: transparent;
}
.navbar__list-elemet a {
  height: auto;
  float: left;
  transition: 0.5s linear;
  position: relative;
  display: block;
  overflow: hidden;
  color: #e9aaaa;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 16px;
  text-align: center;
  margin: 0 5px;
  background: transparent;
  text-transform: uppercase;
  font-weight: 600;
}
@media (max-width: 1400px) {
  .navbar__list-elemet a {
    padding-top: calc(10px + 0 * (100vw - 320px) / 1080);
  }
}
@media (max-width: 1400px) {
  .navbar__list-elemet a {
    padding-bottom: calc(10px + 0 * (100vw - 320px) / 1080);
  }
}
@media (max-width: 1400px) {
  .navbar__list-elemet a {
    padding-left: calc(10px + 5 * (100vw - 320px) / 1080);
  }
}
@media (max-width: 1400px) {
  .navbar__list-elemet a {
    padding-right: calc(10px + 5 * (100vw - 320px) / 1080);
  }
}
@media (max-width: 1400px) {
  .navbar__list-elemet a {
    font-size: calc(10px + 6 * (100vw - 320px) / 1080);
  }
}
@media (max-width: 1078px) {
  .navbar__list-elemet a {
    width: 100%;
    padding: 20 25;
    font-size: 20px;
  }
}
.navbar__list-elemet a:before {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  height: 2px;
  width: 100%;
  border-bottom: 2px solid transparent;
  border-left: 2px solid transparent;
  box-sizing: border-box;
  transform: translateX(100%);
}
.navbar__list-elemet a:after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  border-top: 2px solid transparent;
  border-right: 2px solid transparent;
  box-sizing: border-box;
  transform: translateX(-100%);
}
.navbar__list-elemet a:hover {
  box-shadow: 0 3px 15px hsla(0, 59%, 79%, 0.5);
}
.navbar__list-elemet a:hover:before {
  border-color: #e9aaaa;
  height: 100%;
  transform: translateX(0);
  transition: 0.3s transform linear, 0.3s height linear 0.3s;
}
.navbar__list-elemet a:hover:after {
  border-color: #e9aaaa;
  height: 100%;
  transform: translateX(0);
  transition: 0.3s transform linear, 0.3s height linear 0.5s;
}
