@media (max-width: 1400px) {
  .navbar__reg-button-a {
    padding-right: calc(10px + 6 * (100vw - 320px) / 1080);
  }
}
.navbar__reg-button-a::before {
  content: "";
  position: absolute;
  top: 6px;
  left: -2px;
  width: calc(100% + 4px);
  height: calc(100% - 12px);
  background-color: hsl(0, 0%, 8%);
  transition: 0.3s ease-in-out;
  transform: scaleY(1);
}
.navbar__reg-button-a::after {
  content: "";
  position: absolute;
  left: 6px;
  top: -2px;
  height: calc(100% + 4px);
  width: calc(100% - 12px);
  background-color: hsl(0, 0%, 8%);
  transition: 0.3s ease-in-out;
  transform: scaleX(1);
  transition-delay: 0.5s;
}
.navbar__reg-button-a:hover::before {
