  position: absolute;
  border-radius: 10px;
}
.navbar .menu-btn span::after, .navbar .menu-btn span::before {
  content: "";
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  transition: 0.3s;
  top: 0;
  left: 0;
  border-radius: 10px;
